<!-- 基础垂直方向对称条形图
 -->

<template>
  <div class="demo">
    <div class="box">
      <h3>基础垂直方向对称条形图</h3>
      <div id="container"></div>
    </div>
  </div>
</template>

<script>
import { BidirectionalBar } from '@antv/g2plot'

export default {
  components: {},
  data() {
    return {}
  },
  mounted() {
    // 在 mounted 中构建，防止created 中无法获取标签元素
    const data = [
      { country: '乌拉圭', '2016年耕地总面积': 13.4, '2016年转基因种植面积': 12.3 },
      { country: '巴拉圭', '2016年耕地总面积': 14.4, '2016年转基因种植面积': 6.3 },
      { country: '南非', '2016年耕地总面积': 18.4, '2016年转基因种植面积': 8.3 },
      { country: '巴基斯坦', '2016年耕地总面积': 34.4, '2016年转基因种植面积': 13.8 },
      { country: '阿根廷', '2016年耕地总面积': 44.4, '2016年转基因种植面积': 19.5 },
      { country: '巴西', '2016年耕地总面积': 24.4, '2016年转基因种植面积': 18.8 },
      { country: '加拿大', '2016年耕地总面积': 54.4, '2016年转基因种植面积': 24.7 },
      { country: '中国', '2016年耕地总面积': 104.4, '2016年转基因种植面积': 5.3 },
      { country: '美国', '2016年耕地总面积': 165.2, '2016年转基因种植面积': 72.9 }
    ]

    const BidirectionalBarPlot = new BidirectionalBar('container', {
      data,
      layout: 'vertical',
      xField: 'country',
      yField: ['2016年耕地总面积', '2016年转基因种植面积'],
      yAxis: {
        '2016年耕地总面积': {
          nice: true
        },
        '2016年转基因种植面积': {
          min: 0,
          max: 100
        }
      },
      tooltip: {
        shared: true,
        showMarkers: false
      }
    })

    BidirectionalBarPlot.render()
  },
  methods: {},
  created() {
    // var wrapperElement = '<div style=position:relative;></div>'
    // console.log(document.getElementById('container'))
    // document.getElementById('container').appendChild(wrapperElement)
    // setTimeout(() => {
    // }, 2000)
  }
}
</script>

<style lang="stylus" scoped>
.demo {
  display: flex;
  flex-flow: wrap;

  .box {
    box-shadow: 0px 2px 25px 0px rgba(133, 153, 181, 0.2);
    border-radius: 10px;
    display: inline-block;
    padding: 0 20px 20px;
    margin: 20px;

    h3 {
      line-height: 50px;
      height: 50px;
      text-align: center;
    }
  }

  #container {
    display: block;
    width: 500px;
    height: 300px;
  }

  #containerG2 {
    display: block;
    width: 500px;
    height: 300px;
  }

  #containerGz {
    display: block;
    width: 500px;
    height: 300px;
  }
}
</style>



